






























import { Component, Vue } from 'vue-property-decorator';
import Header from '@/components/Header.vue';
import Footer from "@/components/Footer.vue";
import PromotionListItem from "@/components/Promotion/PromotionListItem.vue";

@Component({
  components: {
    Header,
    Footer,
    PromotionListItem,
  },
})

export default class Promotion extends Vue {
  private pageTitle?: String = '优惠活动';
  private promList?: any[] = [];
  private loading?: boolean = false;
  private finished?: boolean = false;
  private refreshing?: boolean = false;
  private loadData(): void {
    setTimeout(() => {
      if (this.refreshing) {
        this.promList = [];
        this.refreshing = false;
      }
      this.promList = [
        { activity: '元旦礼金大放送', date: '2020.12.15-2021.1.15', img: 'giftMoney' },
        { activity: '元旦礼金大放送', date: '2020.12.15-2021.1.15', img: 'giftMoney' },
        { activity: '元旦礼金大放送', date: '2020.12.15-2021.1.15', img: 'giftMoney' },
        { activity: '元旦礼金大放送', date: '2020.12.15-2021.1.15', img: 'giftMoney' },
        { activity: '元旦礼金大放送', date: '2020.12.15-2021.1.15', img: 'giftMoney' }
      ];
      this.loading = false;
      this.finished = true;
    }, 1000);
  }
  private onRefresh(): void {
    this.finished = false;
    this.loading = true;
    this.loadData();
  }
}
